<template>
<div class="add">
  <el-form ref="form" :model="formData" label-width="80px">
    <el-form-item label="">
      <el-input v-model="formData.title"></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="formData.author"></el-input>
    </el-form-item>


   <el-form-item label="">
      <el-upload class="avatar-uploader" action="https://upload-z1.qiniup.com" 
        :show-file-list="false" :on-success="upload" :data="{token:token}">
        <img v-if="formData.pic" :src="formData.pic" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon" style="line-height: 178px"></i>
      </el-upload>
    </el-form-item>

   
    <el-form-item label=" URL">
      <el-input v-model="formData.pic"></el-input>
    </el-form-item> 

    <el-form-item label="">
      <editor v-model="formData.content" @getText="gettext"></editor>
     
    </el-form-item>

    <el-form-item label="">

        <div>
          <el-radio v-model="formData.slider" label="1" border></el-radio>
          <el-radio v-model="formData.slider" label="0" border></el-radio>
        </div>
    </el-form-item>


    <el-form-item label="">
      <div>
          <el-radio v-model="formData.type" label="0" border></el-radio>
          <el-radio v-model="formData.type" label="1" border></el-radio>
          <el-radio v-model="formData.type" label="2" border></el-radio>          
          <el-radio v-model="formData.type" label="3" border></el-radio>
          <el-radio v-model="formData.type" label="4" border></el-radio>  
          <el-radio v-model="formData.type" label="5" border></el-radio>    
          <el-radio v-model="formData.type" label="6" border></el-radio> 
          <el-radio v-model="formData.type" label="7" border></el-radio> 
          <el-radio v-model="formData.type" label="8" border></el-radio>             
                      
      </div>
    </el-form-item>

    <el-form-item>
      <el-button type="danger" @click="submit"  v-if="$route.name == ''">
          
      </el-button>
      <el-button type="danger" @click="saveEdit" v-if="$route.name ==''">
          
      </el-button>
    </el-form-item> 
  </el-form>
</div>
  
</template>


<script>
import editor from "@/components/wangEditor"

export default {
  name: 'add',
  components:{
    editor,
  },
  data(){
    return{
      formData:{
        
        title:"",
        author:"",
        pic:"",
        content:"",
        slider:"",
        type:"",
      },
      token:""   
    } 
  }, 
  //
  methods:{

    upload(res){
      //
        this.formData.pic = "http://image.yaojunrong.com/"+res.key
    },
    gettext(val){
      this.formData.content = val;
    },
     getToken() {   //token 
          this.$axios.qiniuGet().then(res => {
              this.token = res.data;     
          })
    },   
    submit(){
      this.$axios.post("addNews",this.formData).then((res)=>{
        if(res.code ==200){
          this.$message({
              type: 'success',
              message: ''
          });
        }else{
          this.$message({
              type: 'success',
              message: ''
          });
        }
        setTimeout(()=>{
          this.$router.push("/news/list")
        },1500)
      })
    },
    getDate(){
      this.$axios.get("getIdNews",{_id:this.$route.query._id}).then((res)=>{
        this.formData = res.data
       
      })
    },
    saveEdit(){
      var object = this.formData
      var _id = {}
      object._id = this.$route.query._id
      this.$axios.post("updataNews",object).then((res)=>{
          // console.log(res)
          if(res.code ==200){
            this.$message({
              type: 'success',
              message: ''
          });
        }else{
          this.$message({
              type: 'success',
              message: ''
          });
        }
        setTimeout(()=>{
          this.$router.push("/news")
        },1500)
      })
      var params = this.formData
      _id = this.$route.query._id
    } , 
  
  },
  // 
  created() {
     this.getToken();  
    if(this.$route.name == ""){
      this.getDate();
    }
  },
  watch:{
    $route(to,from,next){
      if(to.name == ''){
        this.formData.title =""
        this.formData.author =""
        this.formData.pic =""
        this.formData.content =""
        this.formData.contentText =""
      }
    }
  }      
}
</script>

<style>
.el-input__inner{
  width: 500px;
}
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
<style scoped>
  .add{
   padding:30px 0px;
  }
  .el-input{
    width: 30%;
  }
</style>





